* {
  box-sizing: border-box;
}
*:before,
*:after {
  box-sizing: border-box;
}
* {
  margin: 0;
  padding: 0;
}
img {
  max-width: 100%;
  max-height: 100%;
}
a {
  color: inherit;
  text-decoration: none;
}
ul,
ol {
  list-style: none;
}
/*
    html {
      height: 100%;
    }
    */
body {
  min-width: 100%;
  min-height: 100vh;
  background-color: #eeeeee;
  background-image: url(./5.jpg);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  filter: alpha(opacity=80);
  -moz-opacity: 0.8;
  opacity: 0.8;
}
@media (min-width: 500px) {
  body {
    background-image: url(./1.jpg);
  }
}
.globalHeader {
  padding: 20px;
}

.clock {
  max-width: 250px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 20px;
  transition: all 0.25s;
  cursor: pointer;
}

.clock:hover {
  transform: scale3d(1.1, 1.1, 1.1);
}

.clock p {
  color: rgb(255, 255, 255);
  text-shadow: 0 0 20px rgba(0, 0, 0, 0.8);

  font-size: 25px;
  letter-spacing: 2px;
}
@media (min-width: 500px) {
  .clock p {
    font-family: "Microsoft Yahei Light", "Microsoft Yahei";
    color: rgb(255, 255, 255);
    text-shadow: 0 0 20px rgba(0, 0, 0, 0.35);
    font-size: 35px;
    letter-spacing: 2px;
  }
}

@media (min-width: 500px) {
  .globalHeader {
    padding: 50px 0;
  }
}
.searchForm {
  display: flex;
  justify-content: space-between;
}
@media (min-width: 500px) {
  /*小于500px的不用管，大于500px与上面的合并*/
  .searchForm {
    max-width: 400px;
    margin-left: auto;
    margin-right: auto; /*只有在最大宽度或者固定宽度时，采用这种方法居中*/
  }
}

/*大于500的不用管，小于500的，与上面合并*/
/*
        @media (max-width: 500px) {
      
        .searchForm {
          background-color: red;
        }
      }
      */

.searchForm > input {
  width: 100%;
  margin-right: 10px;
  height: 40px;
  padding: 0 10px;
  border: 1px solid grey; /*默认会有边框，需要把它覆盖掉*/
  border-radius: 4px;
}
.searchForm > button {
  white-space: nowrap; /*里面的内容文字不换行*/
  padding: 0 28px;
  border: none;
  border-radius: 4px;
  background-color: #0282b9;
  color: white;
  font-size: 16px;
}
.globalMain {
  /*
        display: flex;
        justify-content: center;
        */
  max-width: 900px;
  margin-left: auto;
  margin-right: auto;
}
.siteList {
  margin: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  /* width: 800px; */
}
/*小于500的不管，500px很安全的临界值*/
@media (min-width: 500px) {
  .siteList {
    margin-left: 0;
    margin-right: -25px;
    justify-content: flex-start;
  }
}
@media (min-width: 500px) {
  .siteList * {
    transition: all 0.25s;
  }

  .siteList > li:hover {
    /*li和hover有无空格是有区别的*/
    transform: scale3d(1.1, 1.1, 1.1);
  }
}
.siteList > li {
  margin-bottom: 10px;
}
@media (min-width: 500px) {
  .siteList > li {
    margin-right: 25px;
  }
}
.siteList .site {
  width: 160px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: white;
  border: 1px solid #dddddd;
  border-radius: 4px;
  padding: 20px 0;
  position: relative;
  cursor: pointer; /* 鼠标手型*/
}
.siteList .site > .logo {
  width: 64px;
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 64px;
  text-transform: uppercase;
}
.siteList .site > .link {
  font-size: 14px;
  margin-top: 4px;
}
.siteList .site > .close {
  position: absolute;
  top: 3px;
  right: 7px;
  display: none;
  cursor: default; /*鼠标形状变为默认*/
}
.siteList .site:hover > .close {
  display: block;
}
.siteList .addButton {
  width: 160px;
  border: 1px solid #dddddd;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 20px 0;
}
.siteList .addButton .icon {
  width: 64px;
  height: 64px;
}
.siteList .addButton > .text {
  font-size: 14px;
  margin-top: 4px;
}
.siteList .addButton .icon-wrapper {
  width: 64px;
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
}
